<template>
  <div class="menu">
    <div class="menu-se clearfix">
      <div class="scan fl" @click="dimension">
        <img src="../../assets/images/scan.png" alt class="auto-img" />
      </div>
      <div class="menu-search fl">
        <van-search  placeholder="请输入搜索关键词" @focus="search" />
      </div>
      <div class="vedio fr" @click="pronunciation">
        <img src="../../assets/images/vedio.png" alt class="auto-img" />
      </div>
    </div>


    <div class="meue-head">
      <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item>
          <img src="../../assets/images/baner1.png" class="auto-img ai" alt />
        </van-swipe-item>
        <van-swipe-item>
          <img src="../../assets/images/baner2.png" class="auto-img ai" alt />
        </van-swipe-item>
        <van-swipe-item>
          <img src="../../assets/images/baner3.png" class="auto-img ai" alt />
        </van-swipe-item>
        <van-swipe-item>
          <img src="../../assets/images/baner4.png" class="auto-img ai" alt />
        </van-swipe-item>
      </van-swipe>
    </div>

    <div class="choice clearfix">
      <div class="choice-left fl">精选咖啡</div>
      <div class="choice-right fl">
        <div class="right-top">精选咖啡</div>
        <div class="right-button">各种咖啡，总有征服你的味道</div>
      </div>
    </div>

    <div class="menushow clearfix">
      <div class="menu-box fl">
        <div class="menu-list clearfix">
          <div
            class="menu-list-item"
            v-for="(item, index) in typeData"
            :key="index"
            @click="toggleType(item)"
            :class="{menuboxbg: item.isActive}"
          >
            <div class="menu-content">
              <div class="text" :class="{active: item.isActive}">{{item.typeDesc}}</div>
            </div>
          </div>
        </div>
      </div>

      <div class="menu-product fr">
        <div
          class="product-list"
          v-for="(item, index) in products"
          :key="index"
        >
          <div class="pro-box">
            <div class="pro-img">
              <img class="auto-img" :src="item.smallImg" alt />
            </div>
            <div class="pro-info">
              <div class="pro-text">
                <div class="ch-name">{{item.name}}</div>
                <div class="en-name">{{item.enname}}</div>
              </div>
              <div class="pro-pricespecs clearfix">
                <div class="pro-price fl">￥{{item.price}}</div>
                <div class="specs fr" @click="lookinfo(item.pid)">选规格</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/menu.less";
export default {
  name: "Menu",
  data() {
    return {
      typeData: [],
      products: [],
    };
  },

  created() {
    //获取商品类型数据
    this.getTypeData();
  },

  methods: {
    //获取商品类型数据
    getTypeData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/type",
        //如果get请求，参数需要放在params, 如果是post请求,参数需要放在data
        params: {
          appkey: this.appkey,
        },
      }).then((result) => {
        this.$toast.clear();
        // 
        if (result.data.code == 400) {
          let iconData = [
            {
              type: "latte",
            },
            {
              type: "coffee",
            },
            {
              type: "rena_ice",
            },
          ];
          result.data.result.map((v) => {
            for (let i = 0; i < iconData.length; i++) {
              if (v.type == iconData[i].type) {
                v.icon = iconData[i].icon;
                v.activeIcon = iconData[i].activeIcon;
                v.isActive = false;
                break;
              }
            }
          });

          result.data.result.unshift({
            type: "isHot",
            typeDesc: "推荐",
            isActive: true,
          });
          this.typeData = result.data.result;

          this.getproductdata(this.typeData[0]);
        }
      });
    },
    //切换商品类型
    toggleType(item) {
      if (item.isActive) {
        return;
      }

      this.isLoading = true;

      for (let i = 0; i < this.typeData.length; i++) {
        if (this.typeData[i].isActive) {
          this.typeData[i].isActive = false;
          break;
        }
      }
      item.isActive = true;
      this.getproductdata(item);
    },
    //根据商品类型获取商品数据
    getproductdata(item) {
      this.products = [];

      let data = sessionStorage.getItem(item.type);
      if (data) {
        this.products = JSON.parse(data);
        return;
      }

      let params = {
        key: "type",
        value: item.type,
        appkey: this.appkey,
      };

      if (item.type == "isHot") {
        params.key = "isHot";
        params.value = 1;
      }

      

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        params,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 500) {
            this.products = result.data.result;
            sessionStorage.setItem(item.type, JSON.stringify(this.products));
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //查看商品详情
    lookinfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },
    //搜索
    search(){
      this.$router.push({name:"Search"})
    },
    //二维码
    dimension(){
      this.$toast("此功能暂时不开放")
    },
    //语音
    pronunciation(){
      this.$toast("此功能暂时不开放")
    }

  },
};
</script>

<style lang="less" scoped>
</style>